body{font-family:Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif}h2{margin-bottom:.5rem}.navbar{border:solid #e7e7e7;border-width:0 0 1px}.navbar a,.navbar a:hover{background-image:none}.navbar li{margin-bottom:0}.sidebar h3{font-size:1.2em}.sidebar ul{font-size:1.1em}.sidebar li{margin-bottom:0}.sidebar li>ul{margin-top:0}a{background-image:none;color:#337ab7}a,a:hover{text-decoration:none}a:hover{background-image:linear-gradient(0deg,transparent,transparent 1px,#23527c 0,#23527c 2px,transparent 0);color:#23527c}.about-organizer-image{max-width:200px}a.supporter-page-image{display:block;max-width:400px}.supporter-page-image>a>span{margin-left:unset;margin-right:auto}.talk-content hr{margin-bottom:2rem;margin-top:2rem}